
.pg-registration {
  position: absolute;
  padding-top: 5%;
  height: 100%;
  width: 100%;
  overflow: hidden;
  text-align: center;
  background-color: aqua;
  background: url(/static/img/registration/fon.png) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
.pg-registration__logo {
    height: 160px;
    float: left;
    margin-top: 50px;
}
.pg-registration__hr {
    border: 0;
    border-top: 1px dashed #fcfc;
    margin: 55px 0 10px 0;
    float: left;
    position: relative;
    width: 100%;
}
.pg-registration__padding {
    margin-top: 20px;
    position: relative;
    float: left;
    width: 100%;
}
.pg-registration__remark {
    padding: 100px 279px 100px 70px;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    text-align: left;
    margin-left: 147px;
    margin-top: 172px;
    margin-right: -230px;
    font-size: 18px;
    line-height: 32px;
}
.pg-registration__remark-header {
      color: #fff;
      font-family: "OpenSans";
      font-weight: 600;
      line-height: 32px;
      font-size: 26px;
      display: block;
}
.pg-registration__card-registration {
    margin: 50px 0;
}
