select {
  -webkit-appearance: none;
  -moz-appearance: none; }

select::-ms-expand {
  display: none; }

.wg-desc-example__description {
  width: 100%;
  height: auto;
  border-top: 2px solid #2b2b2b;
  overflow: hidden;
  margin-bottom: 50px; }

.wg-desc-example__button_active {
  color: #2b2b2b !important;
  font-weight: bold;
  border-radius: 0; }

.wg-desc-example__table {
  width: 100%;
  table-layout: auto;
  border-collapse: collapse;
  border-spacing: 0;
  line-height: 18px;
  font-size: 13px; }

.wg-desc-example__tr {
  text-align: left;
  height: 32px; }

.wg-desc-example__th {
  padding: 10px; }

.wg-desc-example__td {
  padding: 10px; }
